body {
  background-color: #000;
  font-family: Poppins;
}

h1 {
  text-align: center;
  color: #fff;
  font-size: 3rem;
  line-height: 80vh;
}

span {
  opacity: 0;
  transition: all 1s;
  display: inline-block;
  transform: translateY(50px);
}

span.fade {
  opacity: 100;
  transform: translateX(0);
}

